import React, { Component } from 'react';
import { Button } from 'antd';
import {
  WeaInput, // 
  WeaInput4Num, // 
  WeaTextarea, // 
  WeaBrowserE8, // e8 
  WeaInput4Request, //
  WeaCheckbox, //
  WeaSelect4Base, //
  WeaUpload, // 
  WeaInput4NumTp, // 
  WeaInput4NumCon, // 
  WeaDatePicker, // 
  WeaTimePicker, // 
} from 'weaCom';

import * as Util from '../api/WdUtil';
import Remark from './Remark';

const ButtonGroup = Button.Group;

const getSpan = (value, valueSpan, url) => {
  value = value ? value.toString() : '';
  valueSpan = valueSpan ? valueSpan.toString() : '';
  const valueArr = value ? value.split(',') : [];
  const valueSpanArr = valueSpan ? valueSpan.split(',') : [];
  let valueSpanStr = [];
  for (let i = 0; i < valueArr.length && valueSpanArr.length === valueArr.length; i++) {
    i !== 0 && valueSpanStr.push(',');
    valueSpanStr.push(<a href={url + valueArr[i]} target='_blank'>{valueSpanArr[i]}</a>);
  }
  return (<div>{valueSpanStr}</div>);
};

class DetailTableCell extends Component {
  render() {
    const { actions, text, record, head, rt, showType, wll, dn, index, canRemark, config } = this.props;
    const et = Util.doWfDetailEditTypeGet(head.editType, dn, record, head.id, wll);
    const ht = parseInt(head.fieldhtmltype);
    const t = parseInt(head.type);
    const dt = head.fielddbtype;
    const { lang, reqId, detailtable, userInfo, wfId, canEditOld } = config;
    const isEN = lang !== undefined && lang !== 7;
    const rowIndex = record.key.toString().indexOf('.') > 0 ? `10000${record.key.replace(/\./ig, '')}` : index;
    // console.log('head',head);
    let buttons = '';
    if (head.buttons) {
      buttons = (
        <span style={{ marginLeft: 2, width: '28%' }}>
          <ButtonGroup size='small'>
            {
              head.buttons.map((button) => {
                return (
                  <Button type='primary' onClick={button.event.bind(this, index, record)}
                    {...(button.props || {})}
                  >
                    {button.name}
                  </Button>
                );
              })
            }
          </ButtonGroup>
        </span>
      );
    }
    let value = text; // (text && text!="undefined")?text:"";
    let valueSpan = record['wdfield' + head.id + 'span'];
    //  -zxt 20170417
    valueSpan = (typeof valueSpan !== 'undefined' && valueSpan !== '') ? valueSpan : value;

    // if (typeof value !== 'undefined' && value !== '') {

    //   if (ht == 1) {
    //    //  value = Util.formatNumber(parseFloat(value), parseInt(head.qfws), 0, head.noZeroEnd);
    //     valueSpan = value;
    //   }
    //   // 
    //   if (head.numCon && ht == 1 && t == 2) {

    //     value = Util.formatNumber(parseInt(value), 0, 1, head.noZeroEnd);
    //     valueSpan = value;
    //   }
    //   if (head.numCon && ht == 1 && t == 3) {

    //     value = Util.formatNumber(parseFloat(value), parseInt(head.qfws), 1, head.noZeroEnd);
    //     valueSpan = value;
    //   }
    //   if (ht == 1 && t == 5) {
    //     //valueSpan = value;
    //      value = Util.formatNumber(parseFloat(value), parseInt(head.qfws), 1, head.noZeroEnd);
    //      valueSpan = value;
    //     // valueSpan = Util.formatNumber(value, parseInt(head.qfws), 1, head.noZeroEnd);
    //   }
    // }

    //  -zxt 20170417
    if (typeof value !== 'undefined' && value !== '') {
      if (ht == 1 && t == 3) {
        value = Util.formatNumber(parseFloat(value), parseInt(head.qfws), 0, head.noZeroEnd);
        valueSpan = value;
      }
      // 
      if (head.numCon && ht == 1 && t == 2) {
        value = Util.formatNumber(parseInt(value), 0, 1, head.noZeroEnd);
        valueSpan = value;
      }
      if (head.numCon && ht == 1 && t == 3) {
        value = Util.formatNumber(parseFloat(value), parseInt(head.qfws), 1, head.noZeroEnd);
        valueSpan = value;
      }
      if (ht == 1 && t == 5) {
        value = Util.formatNumber(parseFloat(value), parseInt(head.qfws), 1, head.noZeroEnd);
        valueSpan = value;
      }
      // console.log("head is ",head," ht and t ",ht,t," value is ",value,"   and span ",valueSpan);
    }
    // 00
    if (head.chDetail) {
      const chDetailArr = head.chDetail.split('_');
      const chDetail = chDetailArr.length === 4 ? parseInt(chDetailArr[3].substring(2)) - 1 : -1;
      return (
        <div>
          <span>{valueSpan}</span>
          <Button
            size='small'
            onClick={() => wfDetail.showChDetail(chDetail, record.key)}
            style={{ marginLeft: 10 }}
          ></Button>
        </div>
      );
    }
    // zxt - 20180202
    const remarkProps = {
      isEN,
      canRemark,
      detailtable,
      reqId,
      userInfo,
      fieldId: head.id,
      dbId: record.dbId,
      onChange: (fieldId, result, callback) => {
        actions.addRemark({
          key: record.key,
          dbId: record.dbId,
          fieldId,
          ...result,
        }, callback);
      },
      filedType: 'edit',
      buttons,
    };
    if (record[`wdfield${head.id}remarkstatus`] !== undefined) {
      remarkProps.remarkStatus = record[`wdfield${head.id}remarkstatus`];
    }
    if (record[`wdfield${head.id}remarkstring`] !== undefined) {
      remarkProps.remarkString = record[`wdfield${head.id}remarkstring`];
    }
    // console.log("valueSpan:",valueSpan);

    // checkTooltip(valueSpan,8,showAll)
    if (et <= 0 || et > 2 || rt === 0 || showType === '2') { //  ||showAll==1 ||config.rt==0
      if (ht == 6) { // 
        return (t == 2 || t == 3
          ? <Remark {...remarkProps}>
            <WeaUpload
              index={rowIndex}
              fieldid={head.id}
              style={{ width: '100%' }}
              head={head}
              reqId={reqId}
              wfId={wfId}
              onChange={action4Value}
              value={value}
              valueSpan={valueSpan}
              listType={t == 2 ? 'picture' : t == 3 ? 'picture-card' : 'text'}
              readOnly={true}
              ht={ht}
              t={t}
              et={et}
            />
          </Remark>
          : <div>
            {getSpan(value, valueSpan, '/cloudstore/system/GetDownloadFile.jsp?'
              + `requestid=${reqId}&fieldid=${head.id}&fileid=`)}
            <input type='hidden' value={value} id={`field${head.id}_${rowIndex}`} />
          </div>
        );
      }
      if (ht == 3 && t == 1 || ht == 3 && t == 17) {
        return (
          <Remark {...remarkProps} filedType='read'>
            {getSpan(value, valueSpan, '/hrm/resource/HrmResource.jsp?id=')}
            <input type='hidden' value={value} id={`field${head.id}_${rowIndex}`} />
          </Remark>
        );
      }
      if (ht == 3 && t == 16 || ht == 3 && t == 152) {
        return (
          <Remark {...remarkProps} filedType='read'>
            {getSpan(value, valueSpan, '/workflow/request/ViewRequest.jsp?requestid=')}
            <input type='hidden' value={value} id={`field${head.id}_${rowIndex}`} />
          </Remark>
        );
      }
      if (ht == 4) {
        return (
          <Remark {...remarkProps} filedType='read'>
            {value == '1' ? '' : ''}
            <input type='hidden' value={value} id={`field${head.id}_${rowIndex}`} />
          </Remark>
        );
      }
      function checkHtml(htmlStr) {
        var reg = /<[^>]+>/g;
        return reg.test(htmlStr);
      }
      function createMarkup() {

        return { __html: valueSpan };
        //
      }
      if (checkHtml(valueSpan)) {
        return (
          <Remark {...remarkProps} filedType='read'>
            <div style={ht == 1 && (t == 3 || t == 5) ? { textAlign: 'right' } : null}
              dangerouslySetInnerHTML={createMarkup()}
            />
            <input type='hidden' value={value} id={`field${head.id}_${rowIndex}`} />
          </Remark>
        );
      } else {
        return (
          <Remark {...remarkProps} filedType='read'>
            <div style={ht == 1 && (t == 3 || t == 5) ? { textAlign: 'right' } : null}>{valueSpan}</div>
            <input type='hidden' value={value} id={`field${head.id}_${rowIndex}`} />
          </Remark>
        );
      }
      // zxt - 20180202
    }
    const action4Target = (e) => {
     
        actions.editCell({
          key: record.key,
          fieldId: head.id,
          value: e.target.value,
          valueSpan: e.target.value,
        });
      
    };
    const action4Check = (e) => {
     
        actions.editCell({
          key: record.key,
          fieldId: head.id,
          value: e.target.checked ? '1' : '0',
          valueSpan: e.target.checked ? '' : '',
        });
      
    };
    const action4Value = (v, vs) => {
  
        actions.editCell({
          key: record.key,
          fieldId: head.id,
          value: v,
          valueSpan: vs,
        });
      

    };
    if (ht == 1 && t == 1) { // 
      if (buttons && buttons != '') {
        return (
          <Remark {...remarkProps}>
            {/* <span> */}
            <WeaInput
              index={rowIndex}
              fieldid={head.id}
              type='text'
              style={{ 'width': '65%' }}
              value={value}
              onBlur={action4Target}
              et={et}
            />
            {/* {buttons} */}
            {/* </span> */}
          </Remark>
        );
      }
      else {
        const maxLength = Number(head.fielddbtype.split('(')[1].split(')')[0]);
        return (
          <Remark {...remarkProps}>
            <WeaInput
              index={rowIndex}
              fieldid={head.id}
              type='text'
              style={{ width: '100%' }}
              value={value}
              onBlur={action4Target}
              maxLength={maxLength}
              et={et}
              dt={dt}

            />
          </Remark>
        );
      }
    }
    if (ht == 1 && t == 2) { // 
      return (
        <Remark {...remarkProps}>
          <WeaInput4Num
            index={rowIndex}
            fieldid={head.id}
            style={{ width: '100%' }}
            value={value}
            onBlur={action4Value}
            et={et}
            step={0}
            isThousand={(head.numCon && true == head.numCon) ? 1 : 0}
          />
        </Remark>
      );
    }
    if (ht == 1 && t == 3) { // 
      // console.log(" 3 qfws:",head.qfws,value);
      return (
        <Remark {...remarkProps}>
          <WeaInput4Num
            index={rowIndex}
            fieldid={head.id}
            style={{ width: '100%' }}
            value={value}
            onBlur={action4Value}
            et={et}
            noZeroEnd={head.noZeroEnd}
            step={parseInt(head.qfws)}
            isThousand={(head.numCon && true == head.numCon) ? 1 : 0}
          />
        </Remark>
      );
    }
    if (ht == 1 && t == 4) { // 
      // console.log(" 4 qfws:",head.qfws,value);
      return (
        <Remark {...remarkProps}>
          <WeaInput4NumTp
            index={rowIndex}
            fieldid={head.id}
            style={{ width: '100%' }}
            value={value}
            onBlur={action4Value}
            et={et}
            step={parseInt(head.qfws)}
          />
        </Remark>
      );
    }
    if (ht == 1 && t == 5) { // 
      // console.log(" 5 qfws:",head.qfws,value);
      return (
        <Remark {...remarkProps}>
          <WeaInput4NumCon
            index={rowIndex}
            fieldid={head.id}
            style={{ width: '100%' }}
            value={value}
            onBlur={action4Value}
            et={et}
            noZeroEnd={head.noZeroEnd}
            step={parseInt(head.qfws)}
          />
        </Remark>
      );
    }
    if (ht == 2) { // 
      return (
        <Remark {...remarkProps}>
          <WeaTextarea
            index={rowIndex}
            fieldid={head.id}
            style={{ width: '100%' }}
            value={value}
            onBlur={action4Target}
            et={et}
          />
        </Remark>
      );
    }
    if (ht == 3 && t == 2) { // 
      // console.log("datapicker :",value);
      return (
        <Remark {...remarkProps}>
          <WeaDatePicker
            index={rowIndex}
            fieldid={head.id}
            style={{ width: '100%' }}
            et={et}
            id={head.id}
            value={value}
            onChange={action4Value}
          />
        </Remark>
      );
    }
    if (ht == 3 && t == 19) { // 
      return (
        <Remark {...remarkProps}>
          <WeaTimePicker
            index={rowIndex}
            fieldid={head.id}
            style={{ width: '100%' }}
            value={value}
            et={et}
            onChange={action4Value}
          />
        </Remark>
      );
    }
    if (ht == 3 && (t == 1 || t == 4 || t == 7 || t == 8 || t == 9
      || t == 12 || t == 13 || t == 16 || t == 17 || t == 18 || t == 22
      || t == 23 || t == 24 || t == 25 || t == 27 || t == 28 || t == 29
      || t == 30 || t == 31 || t == 34 || t == 35 || t == 36 || t == 37
      || t == 38 || t == 52 || t == 53 || t == 54 || t == 55 || t == 57
      || t == 59 || t == 60 || t == 61 || t == 62 || t == 63 || t == 65
      || t == 69 || t == 89 || t == 119 || t == 135 || t == 141 || t == 142
      || t == 152 || t == 160 || t == 161 || t == 162 || t == 164 || t == 171
      || t == 179 || t == 194 || t == 226 || t == 244 || t == 251 || t == 256
      || t == 260 || t == 279 || t == 280)
    ) {
      // 1 
      // 4 
      // 7 
      // 8 
      // 9 
      // 12 
      // 13 
      // 16 
      // 17 
      // 18 
      // 22 
      // 23 
      // 24 
      // 25 
      // 27 
      // 28 
      // 29 
      // 30 
      // 31 
      // 34 
      // 35 
      // 36 
      // 37 
      // 38 
      // 52 
      // 53 
      // 54 
      // 55 
      // 57 
      // 59 
      // 60 
      // 61 
      // 62 
      // 63 
      // 65 
      // 69 
      // 89 
      // 119 
      // 135 
      // 141 
      // 142 
      // 152 
      // 160 
      // 161 
      // 162 
      // 164 
      // 171 
      // 179 
      // 194 
      //226  sap
      // 244 
      // 251 
      // 256 
      // 260 
      // 279 
      // 280 
      return (
        <Remark {...remarkProps}>
          <WeaBrowserE8
            index={rowIndex}
            fieldid={head.id}
            value={value}
            valueSpan={valueSpan}
            isSingle={t != 17 && t != 162}
            ht={ht}
            t={t}
            dt={dt}
            onChange={action4Value}
            et={et}
          />
        </Remark>
      );
    }

    if (ht == 3 && t == 16) { // 
      return (
        <Remark {...remarkProps}>
          <WeaInput4Request
            index={rowIndex}
            fieldid={head.id}
            style={{ width: '100%' }}
            value={value}
            valueSpan={valueSpan}
            id={head.id + '_' + record.dbId}
            isMult={false}
            fieldfieldid={head.id}
            ht={ht}
            t={t}
            dt={dt}
            onChange={action4Value}
            et={et}
          />
        </Remark>
      );
    }
    if (ht == 3 && t == 152) { // 
      return (
        <Remark {...remarkProps}>
          <WeaInput4Request
            index={rowIndex}
            fieldid={head.id}
            style={{ width: '100%' }}
            value={value}
            valueSpan={valueSpan}
            fieldid={head.id}
            isMult={true}
            fieldfieldid={head.id}
            ht={ht}
            t={t}
            dt={dt}
            onChange={action4Value}
            et={et}
          />
        </Remark>
      );
    }
    if (ht == 4) { // check
      return (
        <Remark {...remarkProps}>
          <WeaCheckbox
            index={rowIndex}
            fieldid={head.id}
            defaultChecked={value == '1'}
            onChange={action4Check}
          />
        </Remark>
      );
    }
    if (ht == 5) { // 
      return (
        <Remark {...remarkProps}>
          <WeaSelect4Base
            index={rowIndex}
            fieldid={head.id}
            style={{ width: '100%' }}
            defaultValue={value}
            fieldfieldid={head.id}
            onChange={action4Value}
            datas={head.selValues}
            ht={ht}
            t={t}
            et={et}
          />
        </Remark>
      );
    }
    if (ht == 6) {
      return (
        <Remark {...remarkProps}>
          <WeaUpload
            index={rowIndex}
            fieldid={head.id}
            style={{ width: '100%' }}
            reqId={reqId}
            wfId={wfId}
            head={head}
            onChange={action4Value}
            value={value}
            valueSpan={valueSpan}
            listType={t == 2 ? 'picture' : t == 3 ? 'picture-card' : 'text'}
            ht={ht}
            t={t}
            et={et}
          />
        </Remark>
      );
    }
    return (
      <Remark {...remarkProps}>
        <div>{valueSpan}</div>
        <input type='hidden' value={value} id={`field${head.id}_${rowIndex}`} />
      </Remark>
    );
  }
}

export default DetailTableCell
  ;



