import React from 'react';
import { Modal, Table } from 'antd';

class EditLogByRemark extends React.Component {
  static defaultProps = {
  }
  constructor(props) {
    super(props);
    this.isInstanceMounted = false;
    this.prefixClass = 'wfdetail-remark-log';
  }
  componentDidMount() {
    this.isInstanceMounted = true;
    const { actions } = this.props;
    actions.getEditLogByRemark();
  }
  componentWillReceiveProps(nextProps) {
  }
  componentWillUnmount() {
    this.isInstanceMounted = false;
  }
  onClose = () => {
    const { actions } = this.props;
    actions.showEditLogByRemark && actions.showEditLogByRemark(false);
  }
  render() {
    const { visible = false, columns: _columns, datas = [] } = this.props;
    const columns = _columns.map(c => ({
      ...c,
      onFilter: (value, record) => record[c.dataIndex].indexOf(value) >= 0,
      sorter: (a, b) => (a[c.dataIndex] < b[c.dataIndex] ? -1
        : a[c.dataIndex] > b[c.dataIndex] ? 1 : 0),
    }));
    return (
      <Modal
        width={700}
        title=''
        visible={visible}
        onCancel={this.onClose}
        onOk={this.onClose}
      >
        <Table
          columns={columns}
          dataSource={datas}
          size='middle'
        />
      </Modal>
    );
  }
}

export default EditLogByRemark;



