import React, { Component, PropTypes } from 'react'
import { bindActionCreators } from 'redux'
import { connect } from 'react-redux'
import { Table, Button, Row, Col, Select, Icon, Modal, Tooltip, Checkbox, Switch } from 'antd'
import { WeaInput } from 'weaCom'
import objectAssign from 'object-assign'

import * as DetailActions from '../actions'
import * as Util from '../api/WdUtil'
import DetailTableCell from './DetailTableCell'
import DetailTableTree from './DetailTableTree'
import DetailTableExcelImp from './DetailTableExcelImp'
import EditLogByRemark from './EditLogByRemark';

const Option = Select.Option;
const ButtonGroup = Button.Group;
function isIE() {
    if (!!window.ActiveXObject || "ActiveXObject" in window){
        return true;
    }else{
        return false;
    }
 }
function checkTooltip(name, length, showType, column) {
    const style = {
        color: column.shownamecolor || '#111',
        backgroundColor: column.detailbackgroudcolor || '#e7f3fc',
        padding: "10px 8px",
    }
    let dom = ''
    if (!name) {
        dom = "";
    }
   // let addStr = "";
    let addStrlength = 0;

    let addStr ="<span style ='color:#FF0000;font-size:20px;font-weight:800'>*</span>";
    if(column.head.editType == "2"){
      name = addStr+name;
      addStrlength = addStr.length+3;
    }
    // console.log("name ",name ,"showtype ",showType,"  edittype ",column.head.editType);
    if (showType == "2") {
        dom = <div style={style} dangerouslySetInnerHTML={{ __html: name }} />
    }
   if ((name.length - addStrlength) > length) {
        dom = <Tooltip title={name}><div className="ant-div-mowarp" style={style} dangerouslySetInnerHTML={{ __html: name.substring(addStrlength, length) + "..." }} /></Tooltip>;
    }
    else {
        dom = <div className="ant-div-mowarp" style={style} dangerouslySetInnerHTML={{ __html: name }} />
   }
    const buttons = column.head.titleButtons;
    if (typeof buttons === 'object' && buttons.length) {
        return (
            <div style={style} >
                {dom}
                <ButtonGroup size='small'>
                    {
                        buttons.map((button) => {
                            return (
                                <Button type='primary' onClick={button.event.bind(this, column.key)}
                                    {...(button.props || {})}
                                >
                                    {button.name}
                                </Button>
                            );
                        })
                    }
                </ButtonGroup>
            </div>
        )
    }
    return dom;
}

class DetailTable extends Component {
    componentDidMount() {
        try {
            const { heads, datas, fatherKey, fliterDatas } = this.props;
            const { dn, wfd, width, supDetail } = this.props.config;
            //ant-table-treenode
            Util.doWfDetailResize(dn, width);
            Util.doWfDetailCountCol(wfd, datas, heads);
            Util.doWfDetailFormulaCol(fliterDatas, supDetail, fatherKey, dn);
        } catch (e) {
            //alert("dyf:"+e);
        }
    }
    componentDidUpdate() {
        try {
            const { heads, datas, fatherKey, fliterDatas } = this.props;
            const { dn, wfd, width, supDetail } = this.props.config;
            Util.doWfDetailResize(dn, width);
            Util.doWfDetailCountCol(wfd, datas, heads);
            Util.doWfDetailFormulaCol(fliterDatas, supDetail, fatherKey, dn);
        } catch (e) {
            //alert("dyf:"+e);
        }
    }
    render() {
        const { config, showModal, heads, actions } = this.props;
        const { modal, btnName, width, lang } = config;
        const isEN = lang !== undefined && lang !== 7;
        if (heads.length == 0) return (<div></div>)
        //alert("modal:",modal);
        if (modal || config.supDetail) {
            let modalWidth = width != "" ? width : "90%";
            if (modalWidth.indexOf("%") < 0) modalWidth = parseInt(modalWidth) + 40;
            ////console.log("modalWidth:",modalWidth);
            const btnNewName = isEN ? 'Detail ' : '' + config.dn;
            return (
                <div>
                    {!config.supDetail && <Button style={{ "marginTop": "5px" }} onClick={() => actions.showModal()}>{btnName || btnNewName}</Button>}
                    <Modal title={isEN ? 'Detail' : ''}
                        visible={showModal}
                        onCancel={() => actions.hideModal()}
                        width={modalWidth}
                        footer={[<Button key="back" type="ghost" size="large" onClick={() => actions.hideModal()}>{isEN ? 'Back' : ''}</Button>]}
                    >
                        {this.renderTable()}
                    </Modal>
                </div>
            )
        }
        else {
            //try {
            return this.renderTable();
            //}catch(e) {alert("inner:"+e);}
        }
    }
    renderTable() {
        let { columns, pagination, rowSelection, selTreenode, datas, fliterDatas, fliterPagination, isTreenode, fatherKey,
            editLogByRemarkVisible, editLogByRemarkDatas, editLogByRemarkColumns, expandedRowKeys, detail,mustHideStr
        } = this.props;
        const { heads, actions, loading, config, showType } = this.props;
        const { searchValue, searchingforcolValue, saveLoading, showModal } = this.props;
        const { canRemark, searching, searchingforcol, rt, excelImport, excelOutput, treetable, canAdd, canDel,
            treenode, modal, btnName, dn, wll, width, height, tdatashowonly, reqId, lang } = config;
        const { selectedRowKeys } = rowSelection;
        const isFliter = isTreenode || searchValue != "" || searchingforcolValue != "" || fatherKey != 0;
        let maxWidth = 0;
        let isFixed = false;
        let isShow = true;
        const isEN = lang !== undefined && lang !== 7;
        columns = columns.filter(column => { return column.head.editType !== "4" });
        // console.log("in render !");
        // console.log(this.props);
        columns = columns.map((column, index) => {
            const { fixed, editType, mergeRow } = column.head;
            if (fixed && fixed != "") {
                isFixed = true;
            }
            if (editType == 1 || editType == 2) {
                isShow = false;
            }
            ////console.log("mergeRow&&editType==0",mergeRow&&editType==0," mergeRow&&showType=='3':",mergeRow&&showType=="3");

            if (mergeRow && editType == '0' || mergeRow && showType == "2") { //
                let mergeDatas = isFliter ? fliterDatas : datas;
                let i = 0
                let markDatas = new Array();
                while (i < mergeDatas.length) {
                    let rowSpan = 0;
                    let find = true;
                    for (let j = i + 1; j < mergeDatas.length && find; j++) {
                        if (mergeDatas[i][column.dataIndex] === mergeDatas[j][column.dataIndex]) {
                            rowSpan++;
                        }
                        else {
                            find = false;
                        }
                    }
                    if (rowSpan !== 0) {
                        markDatas[i] = rowSpan + 1;
                        for (let k = i + 1; k < i + rowSpan + 1; k++) {
                            markDatas[k] = 0;
                        }
                        i += rowSpan + 1;
                    }
                    else {
                        markDatas[i] = 1;
                        i++;
                    }
                }
                column.markDatas = markDatas;
                ////console.log("markDatas:",markDatas);
            }
            else {
                column.markDatas = null;
            }
            return column;
        });
        if (showType == "2") { //
            //maxWidth += 50;
        }
        const doMergeRow = (cellCom, index, markDatas) => {
            if (!markDatas) {
                return cellCom;
            }
            let obj = {
                children: cellCom,
                props: {},
            };
            obj.props.rowSpan = markDatas[index];
            return obj;
        }
        //console.log("colconsole.log("columns:",columns);
        let markColumn = 0;
        columns = columns.map((column, index) => {
            if (index > 0) {
                // //console.log("column.head.width:",column.head.width);
                const columnWidth = parseFloat(column.head.width || 150);
                maxWidth += columnWidth;
                // ///console.log("isShow:",isShow);
                if (rt != 0 && !isShow && showType != "2") {
                    //maxWidth += 5;
                    if (window.attachEvent) {
                        //maxWidth += 17;
                    }
                }

                if (!column.head.fixed && markColumn === 0) {
                    markColumn = index;
                    return objectAssign({}, column, {
                        title: checkTooltip(column.title, 20, showType, column),
                        width: columnWidth,
                        render(text, record, index) {
                            ////console.log("text:",text,"record:",record);
                            return doMergeRow(
                                <DetailTableCell
                                    config={config}
                                    canRemark={canRemark}
                                    actions={actions}
                                    text={text}
                                    record={record}
                                    dn={dn}
                                    wll={wll}
                                    index={index}
                                    heads={heads}
                                    head={column.head}
                                    rt={rt}
                                    showType={showType}
                                />,
                                index,
                                column.markDatas
                            )

                        }
                    });
                }
                return objectAssign({}, column, {
                    title: checkTooltip(column.title, 20, showType, column),
                    width: columnWidth,
                    fixed: column.head.fixed || false,
                    render(text, record, index) {
                        ////console.log("text:",text,"record:",record);
                        return doMergeRow(<DetailTableCell
                            config={config}
                            canRemark={canRemark}
                            actions={actions}
                            text={text}
                            record={record}
                            dn={dn}
                            wll={wll}
                            index={index}
                            heads={heads}
                            head={column.head}
                            rt={rt}
                            showType={showType}
                        />,
                            index,
                            column.markDatas
                        )
                    }
                });
            }
            else {
                const columnWidth = (treetable && isFixed) ? 200 : 50;
                maxWidth += columnWidth;
                return objectAssign({}, column, {
                    width: columnWidth,
                    fixed: isFixed ? "left" : false,
                    ...(treetable && !isTreenode && showType !== "2" && rt !== 0 ?
                        { className: 'wfdetail-sn' } : {}),
                    render(text, record, index) {
                        ////console.log("rt:",rt);
                        return doMergeRow(
                            treetable && !isTreenode && showType !== "2" && rt !== 0 ? (
                                <span>{text}&nbsp;
                                <a onClick={() => actions.addChildrenRow({ key: [record.key] })}>
                                        <i className="anticon anticon-plus-circle"></i>
                                    </a>
                                </span>
                            ) : (<span>{text}</span>),
                            index,
                            column.markDatas
                        )
                    }
                })
            }
        });
        //console.log("columns:",columns);

        pagination = objectAssign({}, pagination, {
            onChange(current) {
                ////console.log("go current:",current);
                actions.goPage({ current: current });
            }
        });

        fliterPagination = objectAssign({}, fliterPagination, {
            onChange(current) {
                ////console.log("go current:",current);
                actions.goPage({ current: current });
            }
        });

        // //console.log("pagination:",pagination);
        // //console.log("isFliter:",isFliter);
        rowSelection = objectAssign({}, rowSelection, {
            onChange(checkKeys, checkRows) {
                actions.selRow({ checkKeys: checkKeys });
                ////console.log("selectedRowKeys:",selectedRowKeys);
            }
        });
        if (showType == "0" || showType == "2") {
            if (isFliter) fliterPagination = false;
            else pagination = false;
            ////console.log("showType:",showType);
        }
        ////console.log("pagination:",isTreenode?fliterPagination:pagination);
        ////console.log("rowSelection:",(rt==0||loading||isTreenode)?false:rowSelection);
        ////console.log("dataSource:",isTreenode?fliterDatas:datas);
       //// console.log("columns:",columns);
        ////console.log("slice",[].slice(0));
        ////console.log("columns:",columns," width:",width);
        const noRs = (rt == 0 || loading || isTreenode);
        //if(!noRs) maxWidth += 68;
        const isIE8 = navigator.userAgent.indexOf("MSIE 8.0") > 0;
        const isIE =(!!window.ActiveXObject || "ActiveXObject" in window);
        ////console.log("showType:",showType);
        ////console.log("dn:",dn," pagination:",pagination);
        //if(window.console)console.log("mustHideStr:",mustHideStr," 2:", "" !== mustHideStr," 3:",(mustHideStr  &&  "" !== mustHideStr));
        return (
            <div className={"ant-table-scroll-custom" + dn} style={{ "marginTop": "8px" }}>
                {!isTreenode &&
                    (<Row style={{ "marginBottom": "5px" }}>
                        <Col span="12">
                            {!tdatashowonly &&
                                <span style={{ "float": "left" }}>
                                    <Select
                                        onChange={(value) => actions.editShowType({ showType: value })}
                                        value={showType}
                                        disabled={loading}
                                        style={{ "width": "80px" }}
                                        size="small">
                                        <Option value="0">{isEN ? 'Normal View' : ''}</Option>
                                        <Option value="1">{isEN ? 'Paging View' : ''}</Option>
                                        <Option value="2">{isEN ? 'Display View' : ''}</Option>
                                    </Select>
                                </span>
                            }
                            {searching && (
                                <span style={{ "float": "left", "marginLeft": "8px" }}>
                                    <WeaInput
                                        size="small"
                                        type="text"
                                        style={{ width: "130px" }}
                                        placeholder={isEN ? 'Keyword of datas' : ''}
                                        value={searchValue}
                                        onChange={(value) => actions.changeSearch(value)} />
                                </span>)}
                            {searchingforcol && (
                                <span style={{ "float": "left", "marginLeft": "8px" }}>
                                    <WeaInput
                                        size="small"
                                        type="text"
                                        style={{ width: "130px" }}
                                        placeholder={isEN ? 'Keyword of columns' : ''}
                                        value={searchingforcolValue}
                                        onChange={(value) => actions.changeSearchforCol(value)} />
                                </span>)}
                            {saveLoading && (
                                <Button
                                    style={{ "marginLeft": "10px" }}
                                    type="primary"
                                    size="small"
                                    loading>
                                </Button>)}
                        </Col>
                        <Col span="12" style={{ "textAlign": "right" }}>
                            {rt != 0 &&(mustHideStr  &&  "" !== mustHideStr  &&  "e" != mustHideStr) && (
                                <Switch
                                    checkedChildren={isEN ? 'on' : ''}
                                    unCheckedChildren={isEN ? 'off' : ''}
                                    onChange={actions.hideRow}
                                    defaultChecked={false} />
                            )}
                            {rt != 0 && excelImport
                                && (<Button
                                    onClick={() => {
                                        if (reqId == '-1') {
                                            Modal.confirm({
                                                title: isEN ? 'Notice' : '',
                                                content: isEN ? 'Datas is not saved, going to save now?'
                                                    : '',
                                                okText: isEN ? 'Yes' : '',
                                                cancelText: isEN ? 'No' : '',
                                                onOk: () => {
                                                    actions.doWfSave(detail)
                                                },
                                                onCancel: () => { }
                                            })
                                        } else {
                                            actions.showImpModal()
                                        }
                                    }}
                                    style={{ "marginLeft": "10px" }}
                                    type="primary"
                                    size="small"
                                    disabled={loading}>{isEN ? 'Import' : ''}</Button>)}
                            {excelOutput
                                && (<Button
                                    onClick={() => actions.detailExcelOut(true)}
                                    style={{ "marginLeft": "10px" }}
                                    type="primary"
                                    size="small"
                                    disabled={loading}>{isEN ? 'Export' : ''}</Button>)}
                            {rt != 0 && canAdd
                                && (
                                    <Button
                                        onClick={() => actions.addRow()}
                                        style={{ "marginLeft": "10px" }}
                                        type="primary"
                                        size="small"
                                        disabled={loading}>{isEN ? 'Insert' : ''}</Button>)}
                            {rt != 0 && canAdd
                                && (
                                    <Button
                                        onClick={() => actions.copyRow()}
                                        style={{ "marginLeft": "10px" }}
                                        type="primary"
                                        size="small"
                                        disabled={loading}>{isEN ? 'Copy' : ''}</Button>)}
                            {rt != 0 && canDel
                                && (
                                    <Button
                                        onClick={() => actions.delRow()}
                                        type="primary"
                                        size="small"
                                        style={{ "marginLeft": "10px" }}
                                        disabled={loading || selectedRowKeys.length == 0}>{isEN ? 'Delete' : ''}</Button>)}
                            { // 
                                canRemark
                                    ? [
                                        <Button
                                            onClick={() => actions.showEditLogByRemark(true)}
                                            type="primary"
                                            size="small"
                                            style={{ marginLeft: 10 }}
                                        >{isEN ? 'Change Logs' : ''}</Button>,
                                        <EditLogByRemark
                                            actions={actions}
                                            visible={editLogByRemarkVisible}
                                            columns={editLogByRemarkColumns}
                                            datas={editLogByRemarkDatas}
                                        />,
                                    ]
                                    : null
                            }
                        </Col>
                    </Row>)
                }
                <div className='wea-detail-table'>
                    {isTreenode &&
                        (<div style={{ "width": "280px", "marginRight": "5px", "float": "left" }}>
                            <DetailTableTree actions={actions} datas={datas} treenode={treenode} selTreenode={selTreenode} />
                        </div>)
                    }
                    <div style={isTreenode ? { width: width, "float": "left" } : { width: width }} className={isTreenode ? "ant-table-treenode" + dn : ""}>
                        <Table style={{ "textAlign": "left" }}
                            locale={{
                                filterConfirm: isEN ? 'Confirm' : '',
                                filterReset: isEN ? 'Reset' : '',
                                emptyText: isEN ? 'No Data' : '',
                            }}
                            pagination={isFliter ? fliterPagination : pagination}
                            rowSelection={noRs ? null : rowSelection}
                            loading={loading}
                            dataSource={isFliter ? fliterDatas : datas}
                            columns={columns}
                            scroll={(height && height != "0" && !isIE) ? { x: maxWidth, y: height } : { x: maxWidth }}
                            size="middle"
                            showTotal
                            bordered
                            expandedRowKeys={expandedRowKeys}
                            onExpandedRowsChange={v => actions.setExpandedRowKeys({ expandedRowKeys: v })}
                        />
                    </div>
                </div>
                {excelImport && (<DetailTableExcelImp {...this.props} />)}
            </div>
        )
    }
}

//

//style={isTreenode?{"border":"1px solid #d9d9d9","padding":"5px"}:{}}

DetailTable.propTypes = {
    config: PropTypes.object.isRequired,
    heads: PropTypes.array.isRequired,
    datas: PropTypes.array.isRequired,
    columns: PropTypes.array.isRequired,
    fliterDatas: PropTypes.array.isRequired,
    fliterPagination: PropTypes.object.isRequired,
    pagination: PropTypes.object.isRequired,
    rowSelection: PropTypes.object.isRequired,
    loading: PropTypes.object.isRequired,
    paging: PropTypes.object.isRequired,
    showType: PropTypes.object.isRequired,
    searchValue: PropTypes.object.isRequired,
    searchingforcolValue: PropTypes.object.isRequired,
    saveLoading: PropTypes.object.isRequired,
    selTreenode: PropTypes.object.isRequired,
    isTreenode: PropTypes.object.isRequired,
    showModal: PropTypes.object.isRequired,
    impDatas: PropTypes.array.isRequired,
    impMsg: PropTypes.array.isRequired,
    impLoading: PropTypes.object.isRequired,
    impModal: PropTypes.object.isRequired,
    impPercent: PropTypes.object.isRequired,
    fatherKey: PropTypes.object.isRequired,
    actions: PropTypes.object.isRequired,
    mustHideStr:PropTypes.object,
}

function mapStateToProps(state) {
    return {
        detail: state.detail,
        config: state.detail.config,
        datas: state.detail.datas,
        heads: state.detail.heads,
        columns: state.detail.columns,
        fliterDatas: state.detail.fliterDatas,
        pagination: state.detail.pagination,
        fliterPagination: state.detail.fliterPagination,
        rowSelection: state.detail.rowSelection,
        loading: state.detail.loading,
        paging: state.detail.paging,
        showType: state.detail.showType,
        searchValue: state.detail.searchValue,
        searchingforcolValue: state.detail.searchingforcolValue,
        saveLoading: state.detail.saveLoading,
        selTreenode: state.detail.selTreenode,
        isTreenode: state.detail.isTreenode,
        showModal: state.detail.showModal,
        impDatas: state.detail.impDatas,
        impMsg: state.detail.impMsg,
        impLoading: state.detail.impLoading,
        impModal: state.detail.impModal,
        impPercent: state.detail.impPercent,
        fatherKey: state.detail.fatherKey,
        editLogByRemarkVisible: state.detail.editLogByRemarkVisible,
        editLogByRemarkDatas: state.detail.editLogByRemarkDatas,
        editLogByRemarkColumns: state.detail.editLogByRemarkColumns,
        expandedRowKeys: state.detail.expandedRowKeys,
        mustHideStr:state.detail.mustHideStr,
    }
}

function mapDispatchToProps(dispatch) {
    return {
        actions: bindActionCreators(DetailActions, dispatch)
    }
}

class MyErrorHandler extends React.Component {
    render() {
        return (
            <div className="terrible-error">{this.props.error}</div>
        );
    }
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(DetailTable)


