import React from 'react';
import { Popover, Modal, Input, Button, Alert } from 'antd';

import * as API from '../api/detail';
import RemarkLogField from './RemarkLogField';

const { confirm, warning } = Modal;
const alertType = ['success', 'info', 'warning', 'error'];

class Remark extends React.Component {
  static defaultProps = {
  }
  constructor(props) {
    super(props);
    this.isInstanceMounted = false;
    this.prefixClass = 'wfdetail-remark';
    this.initGetList = false;
    this.state = {
      modalVisible: false,
      content: '',
      columns: [],
      datas: [],
      loading: false,
    };
  }
  componentDidMount() {
    this.isInstanceMounted = true;
    const { filedType, remarkString } = this.props;
    //  0  1 
    filedType === 'read' && this.refs.r1 && $(this.refs.r1).closest('td').css({
      position: 'relative',
      backgroundColor: !this.noRemark(remarkString) ? '#ff0' : 'transparent',
    });
    // 
    !this.noRemark(remarkString) && filedType === 'edit' && this.refs.r2
    && $(this.refs.r2).closest('td').css({
      backgroundColor: '#ff0',
    });
  }
  componentWillReceiveProps(nextProps) {
    const { remarkString, filedType } = nextProps;
    const _noRemark = this.noRemark(this.props.remarkString);
    const noRemark = this.noRemark(remarkString);
    // 
    !_noRemark && noRemark && this.refs.r1 && $(this.refs.r1).closest('td').css({
      backgroundColor: 'transparent',
    });
    // 
    _noRemark && !noRemark && this.refs.r1 && $(this.refs.r1).closest('td').css({
      backgroundColor: '#ff0',
    });
    // 
    filedType === 'edit' && this.refs.r2 && !noRemark
    && $(this.refs.r2).closest('td').css({
      backgroundColor: '#ff0',
    });
  }
  componentWillUnmount() {
    this.isInstanceMounted = false;
  }
  strToList = str => str ? JSON.parse(str) : []
  noRemark = str => this.strToList(str).length === 0
  getLoglist = () => new Promise(resolve => {
    this.setState({ loading: true });
    const { reqId, detailtable, fieldId, dbId } = this.props;
    API.doWfDetailRemarkLog({ reqId, detailtable, fieldId, dbId }).then(res => {
      const { columns, datas } = res;
      this.setState({ columns, datas, loading: false });
      this.initGetList = true;
      resolve(res);
    });
  })
  onAdd = () => {
    const { content } = this.state;
    const { onChange, fieldId, userInfo: { userid, username } } = this.props;
    const value = JSON.stringify({ id: '', userid, username, content });
    typeof onChange === 'function' && onChange(fieldId, { value, isAdd: true }, this.getLoglist);
    this.setState({ content: '' });
  }
  onDel = value => {
    const { onChange, fieldId } = this.props;
    confirm({
      title: '',
      content: '',
      onOk: () => {
        typeof onChange === 'function'
        && onChange(fieldId, { value: JSON.stringify(value), isDel: true }, this.getLoglist);
      },
    });
  }
  // on
  onOk = () => {
    const { content } = this.state;
    !content
    ? warning({ title: '', content: '' })
    : this.onAdd();
  }
  onCancel = () => {
    this.setState({ modalVisible: false, content: '' });
  }
  // get
  getModal() {
    const { modalVisible, content, datas, columns, loading } = this.state;
    const footer = [
      <div>
        <Button key='submit' type='primary' onClick={this.onOk}> </Button>
        <Button key='back' type='ghost' onClick={this.onCancel}> </Button>
      </div>,
      <p className='wfdetail-remark-log-title'></p>,
      <RemarkLogField
        {...this.props}
        {...{ datas, columns, loading }}
        onDel={this.onDel}
      />,
    ];
    return (
      <Modal
        width={700}
        wrapClassName='wfdetail-remark-dialog'
        title=''
        maskClosable={false}
        visible={modalVisible}
        onCancel={this.onCancel}
        footer={footer}
      >
        <Input type='textarea' rows={4} value={content}
          onChange={e => this.setState({ content: e.target.value })}
        />
      </Modal>
    );
  }
  getPopoverProps = remarkList => ({
    trigger: 'hover',
    overlayClassName: `${this.prefixClass}-popover`,
    placement: 'topLeft',
    content: (
      <div className={`${this.prefixClass}-popover-content`} >
        {
          remarkList.map(({ userid, username, time, content }, i) => (
            <Alert
              key={`${userid}_${i}`}
              message={content}
              description={
                <span>
                  <span style={{ marginRight: 5 }}>{username}</span>
                  {time.substr(0, 16)}
                </span>
              }
              type={alertType[i % 4]}
            />
          ))
        }
      </div>
    ),
  })
  render() {
    const { filedType, remarkStatus, remarkString, children, canRemark, buttons } = this.props;
    if (!canRemark || remarkStatus === undefined) return <div>{children}{buttons}</div>;
    const remarkList = this.strToList(remarkString);
    const noRemark = this.noRemark(remarkString);
    const props = {
      className: this.prefixClass,
      title: filedType === 'read' ? '' : '',
    };
    return (
      filedType === 'read'
      ?
      <div {...props}
        ref='r1'
        onClick={() => {
          this.setState({ modalVisible: true });
          !this.initGetList && this.getLoglist();
        }}
      >
        {children}
        {buttons}
        {this.getModal()}
        {remarkStatus === 1 ? <span className={`${this.prefixClass}-red`} /> : null}
      </div>
      : noRemark
      ? <div>{children}{buttons}</div>
      : <Popover { ...this.getPopoverProps(remarkList) }>
        <div {...props} ref='r2'>
          {children}
          {buttons}
        </div>
      </Popover>
    );
  }
}

export default Remark;



