import React from 'react';
import { Table } from 'antd';

// import * as API from '../api/detail';

class RemarkLogField extends React.Component {
  constructor(props) {
    super(props);
    this.isInstanceMounted = false;
    this.prefixClass = 'wfdetail-remark-log';
  }
  componentDidMount() {
    this.isInstanceMounted = true;
    // const { reqId, detailtable, fieldId, dbId } = this.props;
    // this.setState({ loading: true });
    // API.doWfDetailRemarkLog({ reqId, detailtable, fieldId, dbId }).then(res => {
    //   const { columns, datas } = res;
    //   this.setState({ columns, datas, loading: false });
    // });
  }
  componentWillUnmount() {
    this.isInstanceMounted = false;
  }
  getColumns() {
    const { userInfo: { userid }, columns, onDel } = this.props;
    return columns.map(c => ({
      ...c,
      ...(c.dataIndex === 'operate' ? { render: text => (
        text === '0' ? '' : text === '1' ? '' : ''
      ) } : {}),
      onFilter: (value, record) => record[c.dataIndex].indexOf(value) >= 0,
      sorter: (a, b) => (a[c.dataIndex] < b[c.dataIndex] ? -1
        : a[c.dataIndex] > b[c.dataIndex] ? 1 : 0),
    })).concat({
      dataIndex: 'delete',
      title: '',
      render: (text, record) =>
        record.userid === userid
        ? <a
          href='javascript:void(0)'
          onClick={() => onDel(record)}
          style={{ color: '#57c5f7' }}
        ></a>
        : '',
    });
  }
  render() {
    const { datas, loading } = this.props;
    return (
      <div>
        <Table
          columns={this.getColumns()}
          dataSource={datas}
          size='middle'
          loading={loading}
        />
      </div>
    );
  }
}

export default RemarkLogField;



