const setDatasLoop = (datas, newDatas, config, heads) => {
    let tDatas = new Array();
    for (let i = 0; i < datas.length; i++) {
        let data = datas[i];
        let dataBackup = cloneDeep(data);
        let find = false;
        ////console.log("dataBackup:",dataBackup);
        if (newDatas !== undefined) {
            for (let j = 0; j < newDatas.length && !find; j++) {
                let newData = newDatas[j];
                ////console.log("data.key:",data.key," newData.key:",newData.key);
                if (data.key === newData.key) {
                    find = true;
                    ////console.log("heads:",heads);
                    ////console.log("heads:",heads," newData:",newData);
                    data = objectAssign({}, data, newData);

                    heads.map((head) => {
                        ////console.log("head:",head);

                        doWfDetailFieldFliter(head, data);
                    });
                    ////console.log("dataBackup:",dataBackup);
                    ////console.log("data:",data);
                    ////console.log("newData:",newData);
                }
            }
        }

        ////console.log("data:",data);
        if (dataBackup !== null && dataBackup.children && dataBackup.children.length > 0) {
            ////console.log("setDatasLoop:",data.children);
            data.children = setDatasLoop(dataBackup.children, newDatas, config, heads);
        }
        data = Util.doWfDetailCountRow(data, config);
        ////console.log("data:",data);
        tDatas.push(data);
        ////console.log("dyf data:",data);
    }
    ////console.log("dyf datas:",datas);
    return tDatas;
}


