const getEleByName = (value) => {
	const eles = document.getElementsByName(value);
	return (eles && eles.length > 0) ? eles[0].value : "";
}

const getClassByName = (className) => { //tagName,className
	if (document.getElementsByClassName) //
		return document.getElementsByClassName(className);
	else {
		let children = document.getElementsByTagName('*');
		let elements = new Array();
		for (let i = 0; i < children.length; i++) {
			let child = children[i];
			let classNames = child.className.split(' ');
			for (let j = 0; j < classNames.length; j++) {
				if (classNames[j] == className) {
					elements.push(child);
					break;
				}
			}
		}
		return elements;
	}
}

const setEleValue = (name, val) => {
	let inputs = document.getElementsByTagName('input');
	for (let i = 0; i < inputs.length; i++) {
		const input = inputs[i];
		if (input.name && input.name.indexOf(name) == 0) {
			input.value = val;
		}
	}
}

