		////console.log("dn:",dn," fkey:",fkey," chDatas:",chDatas); //," chDn:",chDn
		////console.log("stores[dn]:",stores[dn]);
		const { columns, datas, heads, config } = stores[dn].getState().detail;
		////console.log("columns:",columns," datas:",datas);

		let formulaArr = [];
		for (let i = 0; i < columns.length; i++) {
			const column = columns[i].head;
			////console.log("isTrue:",column.chDetailLink && column.chDetailLink!="" && column.chDetailLinkType && column.chDetailLinkType!="");
			if (column.chDetailLink && column.chDetailLink != "" && column.chDetailLinkType && column.chDetailLinkType != "") {
				////console.log("is in link");
				const chDetailLinkArr = column.chDetailLink.split(".");
				const chDetailLinkDetailArr = chDetailLinkArr[0].split("_");
				const childDn = parseInt(chDetailLinkDetailArr[3].substring(2)) - 1;
				const field = "wd" + chDetailLinkArr[1];
				const fatherField = "wdfield" + column.id;
				////console.log("childDn:",childDn," field:",field," fatherField:",fatherField);
				if (childDn == chDn) {
					////console.log("childDn:",childDn," chDn:",chDn," field:",field," chDatas:",chDatas);
					formulaArr[fatherField] = 0;
					for (let j = 0; j < chDatas.length; j++) {
						const chData = chDatas[j];
						formulaArr[fatherField] += chData[field] ? parseFloat(chData[field]) : 0;
					}
					if (column.chDetailLinkType == "0") { //

					}
					if (column.chDetailLinkType == "1") { //
						formulaArr[fatherField] = formulaArr[fatherField] / chDatas.length;
					}
					////console.log("formulaArr[fatherField]:",formulaArr[fatherField]);
					for (let j = 0; j < datas.length; j++) {
						let data = datas[j];
						////console.log("data.key:",data.key," fkey:",fkey);
						if (data.key == fkey) {
							data[fatherField] = formulaArr[fatherField];
							const { wfd } = config;
							data = Util.doWfDetailCountRow(data, config);
						}
					}
				}
			}
		}
		Init.doAction(DetailActions.initOutDatas, stores[dn], { newDatas: datas, ifNoCover: false, ifNoTrigger: true }, () => {
			////console.log("initOutDatas success!");
			//resolve(true);
		});
