export const doWfDetailCountCol = (wfd, datas, heads) => {
    if (!wfd) return;
    if (!wfd.maincalstr) return;
    const mainCalStrArr = wfd.maincalstr.split(";");
    for (let i = 0; i < mainCalStrArr.length; i++) {
        const mainCalStr = mainCalStrArr[i].split("=");
        if (mainCalStr.length == 2) {
            const fromField = mainCalStr[1].replace("detailfield_", "wdfield"); //
            const toField = mainCalStr[0].replace("mainfield_", "field"); //
            let haveField = false;
            for (let j = 0; j < heads.length && !haveField; j++) {
                if ("wdfield" + heads[j].id == fromField) {
                    haveField = true;
                }
            }
            let sum = 0;
            for (let j = 0; j < datas.length && jQuery("#" + toField).length > 0 && haveField; j++) {
                const data = datas[j];
                let cellValue = 0;
                if (data[fromField]) {
                    cellValue = data[fromField].toString().replace(/,/g, "");
                }
                if (!isNumber(cellValue)) {
                    cellValue = 0;
                }
                //eval("cellValue  = datas["+j+"]."+fromField);

                // if(!cellValue&& cellValue!=0) haveField = false;
                // else {
                // console.log("11 cellValue ", cellValue, "   sum  ", sum);

                cellValue = (cellValue && cellValue != "") ? parseFloat(cellValue) : 0;
               // if(window.console) console.log("22 cellValue ", cellValue,"  sum ",sum);
                //cellValue = (cellValue && cellValue!="")?(cellValue):0;
                // sum = add1(sum, cellValue);
                //sum += cellValue
                sum = new BigNumber(sum);
                sum =sum.plus(new BigNumber(cellValue)).toNumber();
              //  if(window.console) console.log("44   sum  ", sum);
                // }
            }
            //if(datas.length==0) haveField = false;
            //console.log("end  sum  ", sum);
            if (jQuery("#" + toField).length > 0 && haveField) {

                //sum =  sum.toFixed(2);
                jQuery("#" + toField).val(sum);
                if (jQuery("#" + toField).attr("type") == "hidden") {
                    jQuery("#" + toField + "span").html(sum);
                }
            }
        }
    }
}
export const doWfDetailCountRow = (data, config) => {
    //console.log(" ----- config:",config," data:",data);
    //zxt - 20170517  
    const { wfd, wdfl } = config;
    if (wfd && wfd.rowcalstr) { //
        try {
            const rowCalStrArr = wfd.rowcalstr.split(";");
            let obj = { ...data };
            let returnObj = { ...data };
            // console.log("obj 1st  :",obj,"resutn obj 1st ",returnObj);
            for (let i = 0; i < rowCalStrArr.length; i++) {
                const rowCalStr = rowCalStrArr[i].toString().replace(new RegExp("detailfield_", "gm"), "obj.wdfield");
                //let obj = data;
                const rowCalIds = rowCalStrArr[i].toString().match(/\d+/ig) || [];
                wdfl.map(w => {
                    if (rowCalIds.some(rId => rId == w.id)) {
                        if (w.type == 2) {
                            obj['wdfield' + w.id] = obj['wdfield' + w.id] ? parseInt(obj['wdfield' + w.id]) : 0;
                            obj['wdfield' + w.id + 'span'] = obj['wdfield' + w.id + 'span'] ? parseInt(obj['wdfield' + w.id + 'span']) : 0;
                        }
                        if (w.type == 3 || w.type == 5) {
                            obj['wdfield' + w.id] = obj['wdfield' + w.id] ? parseFloat(obj['wdfield' + w.id]) : 0;
                            obj['wdfield' + w.id + 'span'] = obj['wdfield' + w.id + 'span'] ? parseFloat(obj['wdfield' + w.id + 'span']) : 0;
                            // obj['wdfield' + w.id] = obj['wdfield' + w.id] ? (obj['wdfield' + w.id]) : 0;
                            // obj['wdfield' + w.id + 'span'] = obj['wdfield' + w.id + 'span'] ? (obj['wdfield' + w.id + 'span']) : 0;
                        }
                    }
                });
                //eval(rowCalStr);
                let objKey = rowCalStr.split('=')[0].split('.')[1];
                //if(window.console)console.log("objkey :",objKey,"  rowCalStr is :",rowCalStr);
                eval(rowCalStr);
                returnObj[objKey] = obj[objKey];
                wdfl.map(w => {
                    if (rowCalIds.some(rId => rId == w.id)) {
                        //console.log("before returnmap w ",w);
                        // console.log("before returnmap w id ",w.id,"     w id value :",returnObj['wdfield' + w.id]," w type",w.type);
                        if (3 != w.fieldhtmltype && 6 != w.fieldhtmltype && 4 != w.fieldhtmltype && 5 != w.fieldhtmltype) {
                            if (w.type == 5 || w.type == 3) {
                                // console.log("3before value ", returnObj['wdfield' + w.id] ," type is :"+w.type ," id is :",w.id);
                                returnObj['wdfield' + w.id] = returnObj['wdfield' + w.id] ? formatNumber(parseFloat(returnObj['wdfield' + w.id]), parseInt(w.qfws), 0) : 0;
                                returnObj['wdfield' + w.id + 'span'] = formatNumber(parseFloat(returnObj['wdfield' + w.id]), parseInt(w.qfws), 0);
                                // console.log("3after value ", returnObj['wdfield' + w.id]);
                                // returnObj['wdfield' + w.id] = returnObj['wdfield' + w.id] ? formatNumber((returnObj['wdfield' + w.id]), parseInt(w.qfws), 0) : 0;
                                // returnObj['wdfield' + w.id + 'span'] = formatNumber((returnObj['wdfield' + w.id]), parseInt(w.qfws), 0);
                            }
                            if (w.type == 2) {
                                // console.log("4before value ", returnObj['wdfield' + w.id] ," type is :"+w.type ," id is :",w.id);
                                returnObj['wdfield' + w.id] = (returnObj['wdfield' + w.id] ? Math.round(returnObj['wdfield' + w.id]) : 0) + "";
                                returnObj['wdfield' + w.id + 'span'] = returnObj['wdfield' + w.id];
                                // console.log("4after value ", returnObj['wdfield' + w.id]);
                            }
                        }
                    }
                    //console.log("after returnmap w ",w);
                });
                returnObj[objKey + 'span'] = obj[objKey];
                if ("-Infinity" == returnObj[objKey + 'span']) {
                    returnObj[objKey + 'span'] = 0;
                    returnObj[objKey] = 0;
                }
            }
            //   if(window.console)console.log("returnObj end :",returnObj);
            return returnObj;
            //zxt - 20170517  
        } catch (e) {////console.log(e);

        }
    }
    else {
        return data;
    }
}
