export const getDateStr = (n) => {
    var date1 = new Date();

    var date2 = new Date(date1);
    date2.setDate(date1.getDate() + 7);
    var times = date2.getFullYear() + "-" + (date2.getMonth() + 1) + "-" + date2.getDate();
    return times;
}
export const add1 = (arg1, arg2) => {
    let r1, r2, m;
    try { r1 = arg1.toString().split(".")[1].length } catch (e) { r1 = 0 }
    try { r2 = arg2.toString().split(".")[1].length } catch (e) { r2 = 0 }
    m = Math.pow(10, Math.max(r1, r2))
    if(window.console)console.log(" arg1 ",arg1," arg2 ",arg2," r1 ",r1," r2 ",r2," m ",m ," result ",((arg1 * m + arg2 * m) / m));
    return ((arg1 * m + arg2 * m) / m);
}
export const isNumber = (val) => {
    var regPos = /^\d+(\.\d+)?$/; //
    var regNeg = /^(-(([0-9]+\.[0-9]*[1-9][0-9]*)|([0-9]*[1-9][0-9]*\.[0-9]+)|([0-9]*[1-9][0-9]*)))$/; //
    if (regPos.test(val) || regNeg.test(val)) {
        return true;
    } else {
        return false;
    }
}

export const cut1 = (arg1, arg2) => {
    var r1, r2, m, n;
    try { r1 = arg1.toString().split(".")[1].length } catch (e) { r1 = 0 }
    try { r2 = arg2.toString().split(".")[1].length } catch (e) { r2 = 0 }
    m = Math.pow(10, Math.max(r1, r2));
    //last modify by deeka
    //
    n = (r1 >= r2) ? r1 : r2;
    return ((arg1 * m - arg2 * m) / m).toFixed(n);
}
export const mul1 = (arg1, arg2) => {
    var m = 0, s1 = arg1.toString(), s2 = arg2.toString();
    try { m += s1.split(".")[1].length } catch (e) { }
    try { m += s2.split(".")[1].length } catch (e) { }
    return Number(s1.replace(".", "")) * Number(s2.replace(".", "")) / Math.pow(10, m);
}
export const div1 = (arg1, arg2) => {
    var t1 = 0, t2 = 0, r1, r2;
    try { t1 = arg1.toString().split(".")[1].length } catch (e) { }
    try { t2 = arg2.toString().split(".")[1].length } catch (e) { }

    r1 = Number(arg1.toString().replace(".", ""))
    r2 = Number(arg2.toString().replace(".", ""))
    return (r1 / r2) * pow(10, t2 - t1);


}
export const format = (d, fmt) => {
    let o = {
        "M+": d.getMonth() + 1, // 
        "d+": d.getDate(), // 
        "h+": d.getHours(), // 
        "m+": d.getMinutes(), // 
        "s+": d.getSeconds(), // 
        "q+": Math.floor((d.getMonth() + 3) / 3), // 
        "S": d.getMilliseconds() // 
    };
    if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (d.getFullYear() + "").substr(4 - RegExp.$1.length));
    for (let k in o)
        if (new RegExp("(" + k + ")").test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
    return fmt;
}

