        num = Math.floor(num * Math.pow(10, cent) + 0.50000000001);//.. 
        cents = num % Math.pow(10, cent); //. 
        num = Math.floor(num / Math.pow(10, cent)).toString();//. 
        cents = cents.toString();//,. 
