import React, { Component, PropTypes } from 'react'
import { Modal, Button, Upload, message, Icon, Row, Col, Form, Radio, Alert, Progress } from 'antd';
const FormItem = Form.Item;
const RadioButton = Radio.Button;
const RadioGroup = Radio.Group;

class DetailTableExcelImp extends Component {
  componentDidMount() {
    const {actions} = this.props;
    const {impTypeAuth} = this.props.config;
    let impTypeAuthArr = impTypeAuth && impTypeAuth!=""?JSON.parse(impTypeAuth):[0];
    let defaultValue = 0;
    if(impTypeAuthArr.indexOf(0)>=0) {
      defaultValue = 0;
    }
    else if(impTypeAuthArr.indexOf(1)>=0) {
      defaultValue = 1;  
    }
    else if(impTypeAuthArr.indexOf(2)>=0) {
      defaultValue = 2;
    }
    else {
      defaultValue = 0;
    }
    actions.changeImpType(defaultValue);
  }
  render() {
    const {impModal,impDatas,impNoDel,impPercent,impMsg} = this.props;
    const {actions,heads,config,pagination} = this.props;
    const {reqId,wfId,dn,impTypeAuth,tdatashowonly,lang,nodeId} = config;
    const isEN = lang!== undefined && lang !== 7;
    const {current} = pagination;
    const formItemLayout = {
         labelCol: { span: 8 },
         wrapperCol: { span: 16 }
      };
      ////console.log("impMsg:",impMsg);
      ///api/app/no0000012/detail/import
      const uploadProps = {
      name:"excelFile",
      showUploadList:false,
      action:'/cloudstore/app/no0000012/ControlServlet.jsp?action=Action_DoWfDetailExcelImport',
      data: {
        reqId:reqId,
        wfId:wfId,
        nodeId:nodeId,
        heads:JSON.stringify(heads),
        dn:dn
      },
      onChange(info) {
        ////console.log("info:",info);
        
        actions.uploadChange(info);
      }
    }
    ////console.log("impTypeAuth:",impTypeAuth);
    let impTypeAuthArr = impTypeAuth && impTypeAuth!=""?JSON.parse(impTypeAuth):[0];
    let defaultValue = 0;
    if(impTypeAuthArr.indexOf(0)>=0) {
      defaultValue = 0;
    }
    else if(impTypeAuthArr.indexOf(1)>=0) {
      defaultValue = 1;  
    }
    else if(impTypeAuthArr.indexOf(2)>=0) {
      defaultValue = 2;
    }
    else {
      defaultValue = 0;
    }
    ////console.log("impTypeAuthArr:",impTypeAuthArr);
    ////console.log("defaultValue:",defaultValue);
    return (
      <Modal title={isEN ? 'Detail Import' : ''}
        visible={impModal}
        onCancel={()=>actions.hideImpModal()}
        footer={[
              (<Button key="back" type="ghost" size="large" onClick={()=>actions.hideImpModal()}>
                {isEN ? 'Back' : ''}
               </Button>),
              (<Button key="submit" type="primary" size="large" onClick={()=>{tdatashowonly ? actions.goPage({current}) : actions.impExcelDatas()}} 
                disabled={!impDatas || (impDatas && impDatas=="1") || (impDatas && impDatas!="1" && impDatas.length==0) || impMsg.length>0 }>
                {isEN ? 'Confirm' : ''}
               </Button>)
            ]}>
              <Form horizontal>
                <FormItem
                  {...formItemLayout}
                  label={isEN ? 'Template' : ':'}>
                  <a className="ant-form-text" onClick={()=>actions.detailExcelOut(false)} target="_blank">{isEN ? 'Download' : ''}</a>
                </FormItem>
          <FormItem
                  {...formItemLayout}
                  label={isEN ? 'Import File' : ':'}
                  required>
            <Upload {...uploadProps}>
              <Button type="ghost">
                  <Icon type="upload" /> {isEN ? 'Select File' : ':'}
              </Button>
            </Upload>
                </FormItem>
              <FormItem
                {...formItemLayout}
                label={isEN ? 'Import Method' : ':'}>
                <RadioGroup onChange={(e)=>actions.changeImpType(e.target.value)} defaultValue={defaultValue}>
	                {impTypeAuthArr.indexOf(0)>=0 && <RadioButton value={0}>{isEN ? 'Insert' : ''}</RadioButton>}
	                {impTypeAuthArr.indexOf(1)>=0 && <RadioButton value={1}>{isEN ? 'Update' : ''}</RadioButton>}
	                {impTypeAuthArr.indexOf(2)>=0 && <RadioButton value={2}>{isEN ? 'Cover' : ''}</RadioButton>}
	              </RadioGroup>
              </FormItem>
              <FormItem
		            {...formItemLayout}
		            label={isEN ? 'Progressing' : ':'}>
              	<Progress percent={impPercent} status={impDatas=="1"?"exception":(impPercent==100?"success":"active")} />
              </FormItem>
              {impMsg.length>0 && (
               	<FormItem
                  {...formItemLayout}
                  label={isEN ? 'Tip' : ':'}>
                <Alert message={<div>
                  <p>{ isEN ? 'Data error, please correct and re-import:' :
                    ''}</p>
                  {impMsg.map((msg)=>{
                    return (<p>{msg}</p>)
                  })}
                  </div>} type="error" />
               </FormItem>
              )}
              {impDatas=="1" && (
                <FormItem
                  {...formItemLayout}
                  label={isEN ? 'Tip' : ':'}>
                  <Alert message={
                    isEN ?
                    <div>
                    <p>Sorry, EXCEL reads exceptions, perhaps the reason is:</p>
                    <p>1. EXCEL data is too much to be processed, please reduce data and batch import</p>
                    <p>2. EXCEL format is wrong!</p>
                    </div>
                    :
                    <div>
                    <p>EXCEL</p>
                    <p>1EXCEL</p>
                    <p>2EXCEL</p>
                    </div>
                  
                  } type="error" />
                </FormItem>
              )}
              {impDatas && impDatas!="1" && impDatas.length>0 && (
                <FormItem
                  {...formItemLayout}
                  label={isEN ? 'Tip' : ':'}>
                  <Alert message={
                    isEN ?
                    `${impDatas.length} records read successfully , click Confirm to import!`
                    :
                    "EXECL"+impDatas.length+""
                  } type="success" />
                </FormItem>
              )}
              { <FormItem
                {...formItemLayout}
                label={isEN ? 'Instruction' : ':'}>
                <Alert message={
                  isEN ?
                  <div>
                    <p>1. Only allow to import the field data with editable property on current node, and ignore the import operation on the field data without editable property.</p>
                    <p>2. When select Insert import method, Add new field data after old ones (blank at 1st time operation) . It is only applicable before 1st time submit.</p>
                    <p>3. When select Update import method, Update the field data.</p>
                  </div>
                  :
                  <div>
                    <p>1500</p>
                    <p>2</p>
                    <p>3</p>
                    <p>4</p>
                    <p>5</p>
                    <p>6</p>
                  </div>
                } type="info" />
              </FormItem> }
            </Form>
      </Modal>
    )
  }
}

export default DetailTableExcelImp;


