export const editCell = (params) => {
  return (dispatch, getState) => {
    //alert('editCell');\
    const { datas, config } = getState().detail;
    const { dn, tol, rt, canEditOld } = config;
    let oldParm = new Object();
    oldParm.oldvalue = params.value;
    oldParm.oldvalueSpan = params.valueSpan;
    for (let i = 0; i < datas.length; i++) {
      const data = datas[i];
      if (Number(data.key) === Number(params.key)) {
        const getoldvalue = data["wdfield" + params.fieldId] + "";
        const getoldvalueSpan = data["wdfield" + params.fieldId + "span"] + "";
        oldParm.oldvalue = getoldvalue ? getoldvalue : "";
        oldParm.oldvalueSpan = getoldvalueSpan ? getoldvalueSpan : "";

      }
    }
    Storage.setWfDetailIsSave(dn); //

    Storage.setAutoSubmit("");

    // if (params.value != oldParm.oldvalue) {
    if (!params.ifNoTrigger) {
      if (!opQueue) opQueue = linkPromise(); //
      opQueue = opQueue.then((msg) => {
        dispatch({ type: types.EDIT_CELL, params: params }); //heads
        if (false == canEditOld) {
          params.value = oldParm.oldvalue;
          params.valueSpan = oldParm.oldvalueSpan;
          dispatch({ type: types.EDIT_CELL, params: params }); //heads
        }
      }).then(() => {
        dispatch(doSqlTrigger({ key: params.key, "fieldId": params.fieldId }));
      }).then(() => {
        dispatch(doFnaTrigger({ key: params.key }));
      }).then(() => {
        let find = false; //
        for (let i = 0; i < tol.length && !find; i++) {
          const to = tol[i];
          if (to.triggerFieldName == "field" + params.fieldId) {
            find = true;
          }
        }
        if (find && rt != 0) {
          setTimeout(dispatch(wfTrigger({ key: params.key, fieldId: params.fieldId })), 1000);
        }

      }).then(() => {
        wfDetail.doEdit(dn, params.key, params.fieldId, params.value, params.valuespan);
      });
    }
  }
  //}
}

