export const addChildrenRow = (params) => {
  return (dispatch, getState) => {
    const { config: { dn }, datas } = getState().detail;
    const keys = params.key[0].toString().split('.');
    let key = keys[0];
    let arr = datas;
    keys.forEach((k, i) => {
      arr = arr[Number(k) - 1];
      if (!arr.children || arr.children.length === 0) {
        key += `.1`;
      } else {
        const len = arr.children.length;
        const nextKey = keys[i + 1];
        key += `.${nextKey && nextKey <= len ? nextKey : len + 1}`;
        arr = arr.children;
      }
    })
    dispatch({ type: types.ADD_CHILDRENROW, params });
    wfDetail.doAddChildrenRow(dn, key);
  }
}
