const setDbIdsLoop = (datas, newDatas) => {
  for (let i = 0; i < newDatas.length; i++) {
    const newData = newDatas[i];
    let find = false;
    for (let j = 0; j < datas.length && !find; j++) {
      let data = datas[j];
    //  console.log("newData ", newData, "data ", data, ((data.key + "") === (newData.id + "")));
      if ((data.key + "") === (newData.id + "")) {
        find = true;
        data["dbId"] = newData["dbId"];
      }
    }
  //   console.log("out find is :", find);
    // if (find == false && data.children && data.children.length > 0) {
    //   data.children = setDbIdsLoop(data.children, newDatas);
    // }
    if (!find) {
      for (let k = 0; k < datas.length && !find; k++) {
        let data = datas[k];
        if (find == false && data.children && data.children.length > 0) {
          data.children = setDbIdsLoop(data.children, newDatas);
        }
      }
    }
  }
  return datas;
}

