export const saveDetail = (params = {}, callback, force) => {
  return (dispatch, getState) => {
    dispatch({ type: types.START_SAVELOADING });
    const { heads, datas, delDbIds, config, initDatas } = getState().detail;
    const { reqId, wfId, dn, canRemark, detailtable } = config;
    // zxt - 20180206  
    if (canRemark) {
      const remarkValues = [];
      datas.forEach((d, i) => {
        Object.keys(d).forEach(k => {
          if (/wdfield\d+remarkstring/.test(k)) {
            const fieldId = k.replace(/wdfield|remarkstring/g, () => '');
            const statusKey = k.replace('remarkstring', () => 'remarkstatus');
            const { dbId } = d;
            // const remarkList = d[k] ? JSON.parse(d[k]) : [];
            const initData = initDatas.filter(data => data.dbId === dbId);
            if (initData.length === 1) {
              const oldValue = initData[0][`wdfield${fieldId}`];
              const newValue = d[`wdfield${fieldId}`];
              // remarkList.length > 0 &&
              oldValue !== '' && oldValue != newValue && remarkValues.push({
                dbId,
                fieldId,
                remarkStatus: newValue === '' ? 0 : 1,
                oldValue,
                newValue,
              })
            }
          }
        });
      });
      remarkValues.length > 0 && API.doWfDetailEditByRemark({ detailtable, reqId, values: remarkValues });
    }
    // zxt - 20180206 
    if (!opQueue) opQueue = linkPromise();
    let allRead = true;
    heads.map(h => {
      if (h.editType === '1' || h.editType === '2') allRead = false;
    })
    if (params.force) allRead = false;
    if (delDbIds && delDbIds.length > 0) {
      opQueue = opQueue.then(() => {
        return API.doWfDetailDelMultP({
          delDbIds,
          heads,
          reqId,
          wfId,
          dn
        });
      });
    }
    if (!allRead || wfDetail.mustSave()) {
      for (let i = 0; i < datas.length; i += 100) {
        opQueue = opQueue.then((newDatas) => {
          //alert(newDatas);
          //alert(typeof newDatas);

          if (typeof newDatas != "boolean" && newDatas && newDatas.length > 0) {
            //   alert(newDatas);
            // alert(typeof newDatas);
            let datasDbIds = datas;

            datasDbIds = setDbIdsLoop(datasDbIds, newDatas); //ID

            //dbids<=0 //mark
            dispatch({ type: types.SET_DBID, params: { datas: datasDbIds } });
          }
          return API.doWfDetailSaveMultP({
            reqId,
            wfId,
            heads,
            dn,
            values: datas.slice(i, i + 100),
            type: "add",
            startFlag: i
          });
        });
      }
      if (datas.length > 0) {
        opQueue = opQueue.then((newDatas) => {
          if (newDatas && newDatas.error && newDatas.error.length > 0) {
            alert(`[${newDatas.error[0]}],`)
          }
          if (newDatas && newDatas.length > 0) {
            let datasDbIds = datas;
            datasDbIds = setDbIdsLoop(datasDbIds, newDatas); //ID
            dispatch({ type: types.SET_DBID, params: { datas: datasDbIds } });
          }
          return linkPromise;
        });
      }
    }
    opQueue = opQueue.then(() => {
      return new Promise((resolve) => {
        //alert("ok1");
        Storage.setWfDetailIsSave(dn, "0");
        // zxt - 20170517 sessionDatas  reqId
        Storage.setWfDetailNowDatas(dn, datas, reqId);
        //alert("ok2");

        dispatch({ type: types.END_SAVELOADING });
        callback();
        resolve(true);
      });
    }).catch((msg) => {
      const show = "[" + msg + "]";
      alert(show);

      message.error(show);
    });
  }
}

