export const linkFather = (fatherKey) => {
  return (dispatch, getState) => {
    dispatch({ type: types.LINK_FATHER, params: { fatherKey: fatherKey } });
    const { config, fliterDatas } = getState().detail;
    const { defaultRow, reqId } = config;
    let dr = defaultRow;
    if (fliterDatas.length === 0) {
      while (dr > 0 && reqId == "-1") {
        const params = {};
        dispatch({ type: types.ADD_ROW, params });
        dr--;
      }
    }
  }
}

