export const doFnaTrigger = (params) => {
  return (dispatch, getState) => {
    ////console.log("doFnaTrigger:",params);
    // //console.log("getState():",getState());
    const { config, datas } = getState().detail;
    const { fml, reqId } = config;
    if (fml && fml.length > 0) { //
      let fnaParams = {
        requestid: reqId,
        dtl_id: "", //PkId0PkId
        km: "",
        kmField: "",
        cdztlx: "",
        cdztlxField: "",
        cdzt: "",
        cdztField: "",
        rq: "",
        rqField: "",
        ys: "",
        ryField: "",
        bmField: "",
        fbField: "",
        cbzxField: ""
      }
      for (let i = 0; i < fml.length; i++) {
        let obj = fml[i];
        if (obj.fieldType == "1") { //
          fnaParams.kmField = obj.fieldId;
        }
        if (obj.fieldType == "2") { //
          fnaParams.cdztlxField = obj.fieldId;
        }
        if (obj.fieldType == "3") { //
          fnaParams.cdztField = obj.fieldId;
        }
        if (obj.fieldType == "4") { //
          fnaParams.rqField = obj.fieldId;
        }
        if (obj.fieldType == "6") { //
          fnaParams.ryField = obj.fieldId;
        }
        if (obj.fieldType == "7") { //
          fnaParams.bmField = obj.fieldId;
        }
        if (obj.fieldType == "8") { //
          fnaParams.fbField = obj.fieldId;
        }
        if (obj.fieldType == "9") { //
          fnaParams.cbzxField = obj.fieldId;
        }
        fnaParams.dtl_id = reqId == "-1" ? "0" : obj.dtlNumber;
      }
      if (!opQueue) opQueue = linkPromise();
      ////console.log("datas:",datas);
      let newDatas = new Array();
      for (let i = 0; i < datas.length; i++) {
        //data
        let data = cloneDeep(datas[i]);
        // let { key, fatherKey } = datas[i];
        // let data = { key, fatherKey };
        // keys -zxt 20170414
        if (params.key == "" || params.key == data.key || (typeof params.key == 'object' && params.key.length > 0)) {
          opQueue = opQueue.then((msg) => {
            let fnaParamsIn = fnaParams;
            fnaParamsIn.km = data["wdfield" + fnaParamsIn.kmField];
            ////console.log("fnaParams.cdztlxField:",fnaParams.cdztlxField," val:",data["wdfield"+fnaParams.cdztlxField]);
            fnaParamsIn.cdztlx = data["wdfield" + fnaParamsIn.cdztlxField];
            fnaParamsIn.cdzt = data["wdfield" + fnaParamsIn.cdztField];
            fnaParamsIn.rq = data["wdfield" + fnaParamsIn.rqField];
            fnaParams.cdztlx = data["wdfield" + fnaParamsIn.cdztlxField];
            return API.doWfDetailFnaTriggerP(fnaParamsIn);
          }).then((fnaDatas) => {
            ////console.log("fnaDatas:",fnaDatas);
            const arr = fnaDatas.split("|");
            const fnaResult = Util.formatFnaResult(arr[2]);
            if (fnaParams.cdztlx == "0") {
              data["wdfield" + fnaParams.ryField] = fnaResult;
            }
            if (fnaParams.cdztlx == "1") {
              ////console.log("fnaResult:",fnaResult," fnaParams:",fnaParams);
              data["wdfield" + fnaParams.bmField] = fnaResult;
            }
            if (fnaParams.cdztlx == "2") {
              data["wdfield" + fnaParams.fbField] = fnaResult;
            }
            if (fnaParams.cdztlx == "3") {
              data["wdfield" + fnaParams.cbzxField] = fnaResult;
            }
            newDatas.push(data);
            ////console.log("arr:",arr," data:",data);
          });
        }
        else {
          opQueue = opQueue.then((msg) => {
            newDatas.push(data);
          });
        }
      }
      opQueue.then((msg) => {
        ////console.log("doFnaTrigger newDatas:",newDatas);
        dispatch({
          type: types.SET_TRIGGER,
          params: {
            datas: newDatas
          }
        });
      });
      opQueue.catch((e) => {
        alert("" + e);
      })
    }
  }
}

const by = (name, minor) => {
  return (o, p) => {
    let a, b;
    if (o && p && typeof o === 'object' && typeof p === 'object') {
      a = o[name];
      b = p[name];
      if (a === b) {
        return typeof minor === 'function' ? minor(o, p) : 0;
      }
      if (typeof a === typeof b) {
        return a < b ? -1 : 1;
      }
      return typeof a < typeof b ? -1 : 1;
    } else {
      thro("error");
    }
  }
}

