export const addRemark = (params, callback) => (dispatch, getState) => {
  const { config } = getState().detail;
  const { reqId, detailtable, canRemark } = config;
  const { fieldId, dbId } = params;
  if (canRemark) {
    API.doWfDetailRemark({
      detailtable,
      reqId,
      ...params,
    }).then(() => {
      callback().then(res => {
        dispatch({
          type: types.ADD_REMARK, params: {
            ...params,
            remarkString: JSON.stringify(res.datas),
          }
        });
      });
    });
  }
}

export const showEditLogByRemark = (bool = false) => ({
  type: types.SHOW_EDITLOGBYREMARK, bool
});

export const getEditLogByRemark = params => (dispatch, getState) => {
  const { config } = getState().detail;
  const { reqId, detailtable } = config;
  API.doWfDetailEditLogByRemark({ detailtable, reqId }).then(res => {
    const { columns, datas } = res;
    dispatch({
      type: types.SET_EDITLOGBYREMARK, params: {
        editLogByRemarkColumns: columns,
        editLogByRemarkDatas: datas,
      }
    })
  });
};

export const setExpandedRowKeys = (params) => {
  return (dispatch, getState) => {
    const { config: { dn } } = getState().detail;
    dispatch({ type: types.SET_EXPANDROWKEYS, params });
    wfDetail.doExpanded(dn, params.expandedRowKeys);
  }
}


